import React, { useContext, useEffect } from "react";
import { Article } from "./Article";

import { GlobalContext } from "../context/GlobalState";

export const ArticleList = () => {
  const { articles, loading, getArticles } = useContext(GlobalContext);

  useEffect(() => {
    getArticles();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  if (loading) {
    return <img src='https://upload.wikimedia.org/wikipedia/commons/b/b1/Loading_icon.gif' alt="loading..." />
  }
  const unfilteredItems = articles.map((article, index) => (
    <Article key={index} article={article} />
  ));

  return (
    <>
      <h3>Current feed</h3>
      <ul className="list">{unfilteredItems}</ul>
    </>
  );
};
